@keyframes height-change {
  from {
    height: 20rem; }
  to {
    height: 120rem; } }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }
  @media only screen and (max-width: 87.5em) {
    html {
      font-size: 55%; } }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 50%; } }
  @media only screen and (max-width: 37.5em) {
    html {
      font-size: 40%; } }
  @media only screen and (max-width: 25em) {
    html {
      font-size: 35%; } }

body {
  box-sizing: border-box;
  display: flex;
  font-family: "Quicksand", sans-serif;
  font-size: 2.5rem;
  padding: 2rem;
  background: linear-gradient(to right top, #434555, #393C4A);
  height: 100vh;
  color: #DEDEDF;
  letter-spacing: 0.035em; }
  @media only screen and (max-width: 75em) {
    body {
      flex-direction: column;
      align-items: center;
      padding: 4rem 2rem;
      height: 120rem; } }

main {
  margin-left: 2rem;
  width: 73%; }
  @media only screen and (max-width: 112.5em) {
    main {
      width: 70%; } }
  @media only screen and (max-width: 87.5em) {
    main {
      font-size: 65%; } }
  @media only screen and (max-width: 75em) {
    main {
      width: 90%;
      margin-left: 0; } }

.heading-secondary {
  font-size: 3.5rem;
  font-weight: 400; }

.btn__item {
  background-color: transparent;
  position: relative;
  border: 1px solid #63D569;
  border-radius: 50%;
  color: #63D569;
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
  z-index: 1000; }
  .btn__item--red {
    border: 1px solid #FE5555;
    color: #FE5555; }
    .btn__item--red:hover {
      background-color: #FE5555;
      transform: translateY(-3px);
      box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
      color: #DEDEDF; }
  .btn__item i {
    font-size: 1.5rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .btn__item:active {
    transform: translateY(0px);
    box-shadow: none; }
  .btn__item:hover:not(:last-child) {
    background-color: #63D569;
    transform: translateY(-3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
    color: #DEDEDF; }

#openInfo {
  display: none;
  background-color: transparent;
  font-size: 4rem;
  color: #DEDEDF;
  border: none;
  cursor: pointer;
  outline: none; }
  @media only screen and (max-width: 75em) {
    #openInfo {
      display: block;
      margin: 2rem auto 0; } }

/* ===========================================================
====================INFORMATION SIDEBAR ======================
==============================================================*/
.information {
  padding: 5rem;
  width: 27%;
  background-color: rgba(125, 127, 137, 0.6);
  border-radius: 2.5rem; }
  @media only screen and (max-width: 87.5em) {
    .information {
      width: 35%; } }
  @media only screen and (max-width: 75em) {
    .information {
      width: 90%;
      padding: 3rem 7rem;
      margin-bottom: 3rem;
      display: flex;
      flex-direction: column; } }

.date {
  text-align: center;
  position: relative;
  margin-bottom: 7rem; }
  @media only screen and (max-width: 75em) {
    .date {
      margin-bottom: 3rem;
      width: 100%; } }
  .date__time {
    font-size: 4rem;
    margin-top: 2rem; }

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem; }
  .total__num {
    font-size: 3.5rem; }

.tasks-information {
  margin-bottom: 7rem;
  position: relative; }
  @media only screen and (max-width: 75em) {
    .tasks-information {
      margin-bottom: 3rem; } }
  .tasks-information__priorities {
    margin-bottom: 4rem; }
  .tasks-information__priority {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.5rem;
    margin-left: 3rem; }
    .tasks-information__priority:not(:last-child) {
      margin-bottom: 3rem; }
    .tasks-information__priority::before {
      position: absolute;
      content: '';
      display: block;
      width: 1rem;
      height: 1rem;
      background-color: red;
      border-radius: 50%;
      left: -7%; }
    .tasks-information__priority:nth-child(2)::before {
      background-color: #F2E534; }
    .tasks-information__priority:nth-child(3)::before {
      background-color: #64E268; }

.efficiency {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column; }
  .efficiency__chart {
    position: relative;
    fill: rgba(0, 0, 0, 0);
    stroke: #4B86EC;
    stroke-width: 5; }
  .efficiency__diagram {
    margin-top: 2rem; }
  .efficiency__perc {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%); }

/* ===========================================================
======================== TASKS FIELD =========================
==============================================================*/
.add-task {
  background-color: rgba(125, 127, 137, 0.6);
  border-radius: 2.5rem;
  height: 23rem;
  padding: 5rem;
  margin-bottom: 2rem; }
  @media only screen and (max-width: 25em) {
    .add-task {
      padding: 3rem; } }

.add__label--right {
  margin-right: 3.4rem; }

.add__block {
  display: flex;
  align-items: center; }
  .add__block:first-child {
    margin-bottom: 2rem; }

.add__input {
  font-family: "Quicksand", sans-serif;
  width: 93rem;
  padding: 1.5rem;
  border: none;
  outline: none;
  border-radius: 1.5rem;
  color: #434555;
  font-size: 2rem;
  background-color: #DEDEDF;
  opacity: 0.5;
  margin: 0 1.5rem; }
  @media only screen and (max-width: 25em) {
    .add__input {
      width: 21rem; } }

.add__btn {
  width: 16rem;
  height: 5.5rem;
  border: 1px solid #64E268;
  border-radius: 1.5rem;
  background-color: transparent;
  outline: none;
  transition: all 0.3s ease;
  cursor: pointer; }
  .add__btn i {
    color: #64E268; }
  .add__btn:hover {
    background-color: #64E268;
    transform: translateY(-3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5); }
    .add__btn:hover i {
      color: #434555; }
  .add__btn:active {
    transform: translateY(0px);
    box-shadow: none; }

.add__select-block {
  position: relative; }
  .add__select-block i {
    position: absolute;
    font-size: 1.5rem;
    color: #434555;
    top: 37%;
    left: 79.5%;
    z-index: 10; }
  .add__select-block:before {
    position: absolute;
    content: '';
    display: block;
    width: 1rem;
    height: 1rem;
    background-color: red;
    border-radius: 50%;
    z-index: 20;
    top: 42%;
    left: 11%; }
  .add__select-block--red::before {
    background-color: #FE5555; }
  .add__select-block--middle::before {
    background-color: #F2E534; }
  .add__select-block--low::before {
    background-color: #64E268; }

.add__select {
  position: relative;
  font-family: "Quicksand", sans-serif;
  font-size: 2rem;
  width: 15rem;
  padding: 1.5rem 1.5rem 1.5rem 3.7rem;
  border: none;
  outline: none;
  border-radius: 1.5rem;
  color: #434555;
  background-color: #DEDEDF;
  appearance: none;
  opacity: 0.5; }

.tasks {
  display: flex;
  justify-content: space-between; }
  @media only screen and (max-width: 75em) {
    .tasks {
      flex-direction: column; } }
  .tasks__list {
    width: 32.5%; }
    @media only screen and (max-width: 75em) {
      .tasks__list {
        width: 100%; }
        .tasks__list:not(:last-child) {
          margin-bottom: 3rem; } }
  .tasks__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(125, 127, 137, 0.6);
    border-radius: 2.5rem;
    padding: 3.5rem;
    margin-bottom: 2rem;
    width: 100%; }
    .tasks__item--high::after {
      position: absolute;
      content: '';
      display: block;
      height: 2px;
      width: 82%;
      background-color: #FE5555;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%); }
    .tasks__item--middle::after {
      position: absolute;
      content: '';
      display: block;
      height: 2px;
      width: 82%;
      background-color: #F2E534;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%); }
    .tasks__item--low::after {
      position: absolute;
      content: '';
      display: block;
      height: 2px;
      width: 82%;
      background-color: #64E268;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%); }
  .tasks__text-block {
    width: 25rem; }

@media only screen and (max-width: 75em) {
  .hidden {
    display: none; } }
